<template>
  <view class="main">
    <view class="hand">
      <image src="../../static/tabBarimg/dbleft.png" @click="blackbtn"></image>
      包月修改费率
    </view>
    <view class="show">
      <view class="sone">
        <input placeholder="输入套餐名字" v-model="packageName" maxlength="50" />
      </view>
      <van-tabs v-model="active" @disabled="onClickDisabled">
        <van-tab title="计时" :disabled="hat">
          <view class="stap">
            <view class="stapone">
              <view class="staponeleft"> 收费标准： </view>
              <input type="digit" placeholder="元/月" v-model="monthStandard" maxlength="8" />
              <view class="staponeleft1"> 元/月 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 每月限制： </view>
              <input type="digit" placeholder="小时" v-model="monthNum" v-input-limit="0" maxlength="6" />
              <view class="staponeleft1"> 小时 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 加油包单价： </view>
              <input type="digit" placeholder="元" v-model="comeStandard" maxlength="8" />
              <view class="staponeleft1"> 元 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 加油包小时数： </view>
              <input type="digit" placeholder="小时" v-model="comeNum" v-input-limit="0" maxlength="6" />
              <view class="staponeleft1"> 小时 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 允许同时充电次数： </view>
              <van-stepper v-model="numtitle" max="5" disable-input />
              <view class="staponeleft1"> 次 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft1" style="margin-left: 0upx; color: red"> 允许一个用户同时能充的最大次数，默认为1，最大为5； </view>
            </view>
            <view class="staptwo">
              <view class="staptwohand">备注：（100字）</view>
              <textarea maxlength="100" v-model="remarks" />
            </view>
            <view class="stapthree">
              <view class="stapthreeleft" @click="xinzbtn">保存</view>
              <view class="stapthreeright" @click="deletebtn">删除</view>
              <!-- <view class="stapthreezhong" >保存</view> -->
            </view>
          </view>
        </van-tab>
        <van-tab title="计次" :disabled="hat1">
          <view class="stap">
            <view class="stapone">
              <view class="staponeleft"> 收费标准： </view>
              <input type="digit" placeholder="元/月" v-model="monthStandard" maxlength="8" />
              <view class="staponeleft1"> 元/月 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 每月限制： </view>
              <input type="digit" placeholder="次" v-model="monthNum" v-input-limit="0" maxlength="6" />
              <view class="staponeleft1"> 次 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 加油包单价： </view>
              <input type="digit" placeholder="元" v-model="comeStandard" maxlength="8" />
              <view class="staponeleft1"> 元 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 加油包次数： </view>
              <input type="digit" placeholder="次" v-model="comeNum" v-input-limit="0" maxlength="6" />
              <view class="staponeleft1"> 次 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft"> 允许同时充电次数： </view>
              <van-stepper v-model="numtitle" max="5" disable-input />
              <view class="staponeleft1"> 次 </view>
            </view>
            <view class="stapone">
              <view class="staponeleft1" style="margin-left: 0upx; color: red"> 允许一个用户同时能充的最大次数，默认为1，最大为5； </view>
            </view>
            <view class="staptwo">
              <view class="staptwohand">备注：（100字）</view>
              <textarea maxlength="100" v-model="remarks" />
            </view>
            <view class="stapthree">
              <view class="stapthreeleft" @click="xinzbtn">保存</view>
              <view class="stapthreeright" @click="deletebtn">删除</view>
              <!-- <view class="stapthreezhong" >保存</view> -->
            </view>
          </view>
        </van-tab>
      </van-tabs>
    </view>
  </view>
</template>

<script>
import Toast from '../../wxcomponents/vant/toast/toast';
	import Dialog from '../../wxcomponents/vant/dialog/dialog';
import { section } from "../../common/section.js"
export default {
  components: {},
  mounted() {},

  data() {
    return {
      packageId: "",
      active: "",
      packageName: "", //套餐名
      monthStandard: 0, //包月金额
      monthNum: 0, //包月次数/时长（小时）
      comeStandard: 0, //加油包金额
      comeNum: 0, //加油包次数/时长
      remarks: "", //备注
      type: "",
      hat: false,
      hat1: true,
      numtitle: 1, //允许最大购买次数
    }
  },
  onLoad(options) {
    this.packageId = options.packageId
    this.type = options.type
    if (this.type == "A1") {
      this.active = 0
      this.hat = false
      this.hat1 = true
    } else if (this.type == "A3") {
      this.active = 1
      this.hat = true
      this.hat1 = false
    }
  },
  onShow() {
    this.show()
  },
  methods: {
    // 获取一个费率套餐信息
    show() {
      this.$base
        .request1("manager/rate" + "/" + this.packageId, "GET")
        .then(res => {
          var item = res.data.data[0]
          this.packageName = item.packageName
          this.monthStandard = item.monthStandard
          this.monthNum = item.monthNum
          this.numtitle = item.limitNum
          if (item.comeStandard) {
            this.comeStandard = item.comeStandard
          } else {
            this.comeStandard = 0
          }
          if (item.comeNum) {
            this.comeNum = item.comeNum
          } else {
            this.comeNum = 0
          }
          this.remarks = item.remarks
        })
        .catch(err => {})
    },
    //删除一个费率
    deletebtn() {
      Dialog.confirm({
        message: "确认删除该费率？",
      })
        .then(() => {
          this.$base
            .request1("manager/rate" + "/" + this.packageId, "DELETE")
            .then(res => {
              if (res.data.code == 200) {
                this.$toast.success("删除成功")
                uni.navigateBack()
              } else {
                this.$toast.fail(res.data.msg)
                return
              }
            })
            .catch(err => {})
        })
        .catch(() => {
          // on cancel
        })
    },
    //添加一个费率套餐
    xinzbtn() {
      Dialog.confirm({
        title: "提示",
        message: "请尽量不要修改包月费率，否则可能会引起包月用户无法使用",
      })
        .then(() => {
          let data = {}
          if (this.active == 0) {
            if (!this.packageName) {
              this.$toast.fail("套餐名不能为空")
              return
            }
            data = {
              packageName: this.packageName,
              type: "A1",
              minMoney: 0,
              lossRate: 0,
              isFund: 0,
              remarks: this.remarks,
              monthStandard: this.monthStandard,
              monthNum: this.monthNum,
              comeStandard: this.comeStandard,
              comeNum: this.comeNum,
              limitNum: this.numtitle,
              defaultAutoStop: 0,
              isAutoStop: 0,
            }
          } else if (this.active == 1) {
            if (!this.packageName) {
              this.$toast.fail("套餐名不能为空")
              return
            }

            data = {
              packageName: this.packageName,
              type: "A3",
              minMoney: 0,
              lossRate: 0,
              isFund: 0,
              remarks: this.remarks,
              monthStandard: this.monthStandard,
              monthNum: this.monthNum,
              comeStandard: this.comeStandard,
              comeNum: this.comeNum,
              limitNum: this.numtitle,
              defaultAutoStop: 0,
              isAutoStop: 0,
            }
          }

          let header = {
            "content-type": "application/json ",
          }
          this.$base
            .request1("manager/rate" + "/" + this.packageId, "PUT", data, header)
            .then(res => {
              if (res.data.code == 200) {
                this.$toast.success("修改成功")
                uni.navigateBack()
              } else {
                this.$toast.fail(res.data.msg)
                return
              }
            })
            .catch(err => {})
        })
        .catch(() => {
          // on cancel
        })
    },
    // onClick(){
    // 	this.packageName='',
    // 	this.remarks='',
    // 	this.monthStandard=0,
    // 	this.monthNum=0,
    // 	this.comeStandard=0,
    // 	this.comeNum=0
    // },
    //返回上一页
    blackbtn() {
      uni.navigateBack()
    },
    onClickDisabled() {
      this.$toast.fail("包月模式不允许切换类型")
    },
  },
}
</script>

